<template>
	<div id="productionSales" v-title data-title="产销对接">
		<div class="building">建设中...</div>
		<effect-background></effect-background>
	</div>
</template>
<script>
import effectBackground from '@/components/common/effect-background.vue';
export default {
	name: 'productionSales',
	data() {
		return {};
	},
	mounted() {},
	methods: {},
	components: {
		effectBackground,
	},
};
</script>
<style scoped lang="scss">
#productionSales {
	position: relative;
}
.building {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 36px;
	color: #00f6ff;
	text-align: center;
	&:before {
		content: '.';
		font-size: 0;
		display: inline-block;
		width: 100%;
		height: 100%;
		font-size: 0;
		background: url(/static/img/overview-map-bg.e0245c6.png) center no-repeat;
		background-size: 100% 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
}
</style>



